import React, { Component } from 'react';
import "./pageTitle.scss";

class PageTitle extends Component{
    render(){
        return(
            <section id="pageTitle">
                <h1>{ this.props.title }</h1>
                <h6 className="desc">{ this.props.desc }</h6>
            </section>
        )
    }
}

export default PageTitle