import React, { Component } from 'react';

class RatingUnit extends Component{
    getBlocks = () => {
        const noBlocks = 10
        const times = Math.round((this.props.value / 100) * noBlocks);
        let element = [];
        for (let index = 0; index < times - 1; index++) {
            element.push(
				<div
                    className="block"
					key={index}
					style={{ width: 100 / noBlocks + "%" }}
				></div>
			);
        }
        element.push(
            <div
                key={times}
                className="block">
				{this.props.value}
			</div>
		);
        return element;
        
    }

    render(){
        return (
            <li
                className="rating">
				<p className="name">{this.props.name}</p>
				<div className="bar">
                    {this.getBlocks()}
                </div>
			</li>
		);
    }
}

class Rating extends Component{
    render(){
        return(
            <div className="rating">
                <h3>{this.props.name}</h3>
                <hr />
                <ul>
                    {this.props.data.map(inp => <RatingUnit key={inp[0]} name={inp[0]} value={inp[1]}/> )}
                </ul>
            </div>
        )
    }
}

export default Rating