import React from "react";
import ReactDOM from "react-dom";

import Home from "./page/home";
import About from "./page/about";
import Contact from "./page/contact";
import Projects from "./page/projects";
import My404 from "./page/404";

import Nav from "./component/nav";
import PageTitle from "./component/pageTitle";
import PageFooter from "./component/footer";
import { Switch, Route, HashRouter } from "react-router-dom";

import "./style.scss";

import Akropol from "./page/projects/akropol";
import Ankara from "./page/projects/ankara";
import Arduino from "./page/projects/arduino";
import Bead from "./page/projects/bead";
import Bliss from "./page/projects/bliss";
import Charms from "./page/projects/charms";
import Danfo from "./page/projects/danfo";
import Flipaxis from "./page/projects/flipaxis-logo";
import Interrogation from "./page/projects/interrogation-room";
import Lily from "./page/projects/lily-collection";
import NIMC from "./page/projects/nimc";
import TIN from "./page/projects/this-is-nigeria";
import Mvsuv from "./page/projects/mv-suv";

class Base extends React.Component {
	state = {
		page: {
			title: "Project",
			description: ""
		}
	};

	componentDidMount(){
		window.addEventListener("resize", function(e){
			if (e.target.innerWidth > 768)
			{
				document.getElementById("root").classList.remove("openMobileMenu");
			}
		});
		
		// document.body.addEventListener("scroll", this.SetMobileMenuPosition);
		window.addEventListener("scroll", this.SetMobileMenuPosition);
	}
	
	SetMobileMenuPosition = () => {
		const nav = document.getElementById("nav").getBoundingClientRect();
		
		let topPos = nav.top + nav.height / 2;
		let halfHeight = window.innerHeight / 2;
		
		const dropDown = document.getElementById("lv2");
		if (topPos > halfHeight) dropDown.classList.add("up");
		else dropDown.classList.remove("up");
	};
	
	pageProperties = input => {
		this.setState({ page: input });
		setTimeout(this.SetMobileMenuPosition);
	};

	render() {
		document.title = `${this.state.page.title} | Theophilus`;
		window.scrollTo(0, 0);
		return (
			<HashRouter>
				<Nav />
				<section id="page">
					<PageTitle
						title={this.state.page.title}
						desc={this.state.page.description}
					/>
					<section id="pageContent">
						<Switch>
							<Route
								path="/"
								exact
								render={props => (
									<Home
										{...props}
										pageProperties={this.pageProperties}
									/>
								)}
							/>
							<Route
								path="/about"
								render={props => (
									<About
										{...props}
										pageProperties={this.pageProperties}
									/>
								)}
							/>
							<Route
								path="/projects"
								exact
								render={props => (
									<Projects
										{...props}
										pageProperties={this.pageProperties}
									/>
								)}
							/>
							<Route
								path="/contact"
								render={props => (
									<Contact
										{...props}
										pageProperties={this.pageProperties}
									/>
								)}
							/>

							<Route
								path="/projects/akropol"
								render={props => (
									<Akropol
										{...props}
										pageProperties={this.pageProperties}
									/>
								)}
							/>
							<Route
								path="/projects/ankara"
								render={props => (
									<Ankara
										{...props}
										pageProperties={this.pageProperties}
									/>
								)}
							/>
							<Route
								path="/projects/arduinoSmartCar"
								render={props => (
									<Arduino
										{...props}
										pageProperties={this.pageProperties}
									/>
								)}
							/>
							<Route
								path="/projects/bead"
								render={props => (
									<Bead
										{...props}
										pageProperties={this.pageProperties}
									/>
								)}
							/>
							<Route
								path="/projects/bliss"
								render={props => (
									<Bliss
										{...props}
										pageProperties={this.pageProperties}
									/>
								)}
							/>
							<Route
								path="/projects/charms"
								render={props => (
									<Charms
										{...props}
										pageProperties={this.pageProperties}
									/>
								)}
							/>
							<Route
								path="/projects/danfo"
								render={props => (
									<Danfo
										{...props}
										pageProperties={this.pageProperties}
									/>
								)}
							/>
							<Route
								path="/projects/flipaxisLogo"
								render={props => (
									<Flipaxis
										{...props}
										pageProperties={this.pageProperties}
									/>
								)}
							/>
							<Route
								path="/projects/interrogationRoom"
								render={props => (
									<Interrogation
										{...props}
										pageProperties={this.pageProperties}
									/>
								)}
							/>
							<Route
								path="/projects/lily"
								render={props => (
									<Lily
										{...props}
										pageProperties={this.pageProperties}
									/>
								)}
							/>
							<Route
								path="/projects/nimc"
								render={props => (
									<NIMC
										{...props}
										pageProperties={this.pageProperties}
									/>
								)}
							/>
							<Route
								path="/projects/tin"
								render={props => (
									<TIN
										{...props}
										pageProperties={this.pageProperties}
									/>
								)}
							/>
							<Route
								path="/projects/s3v"
								render={props => (
									<Mvsuv
										{...props}
										pageProperties={this.pageProperties}
									/>
								)}
							/>
							<Route
								render={props => (
									<My404
										{...props}
										pageProperties={this.pageProperties}
									/>
								)}
							/>
						</Switch>
					</section>
				</section>
				<PageFooter />
			</HashRouter>
		);
	}
}

ReactDOM.render(<Base />, document.getElementById("root"));
