import React, { Component } from "react";
import { Link } from "react-router-dom";

import NavLinks from "./navLinks";

import "./nav.scss";

class Nav extends Component {
	state = {
		pageName: ["Home"]
	};

	changeState = pageName => {
		this.setState({ pageName: [pageName] });
		document.getElementById("root").classList.remove("openMobileMenu");
	};

	openCloseMobileMenu = () => {
		const main = document.getElementById("root").classList;
		if (main.contains("openMobileMenu")) main.remove("openMobileMenu");
		else main.add("openMobileMenu");
	};

	render() {
		return (
			<nav id="nav">
				<Link to="/">
					<div id="logo"></div>
				</Link>
				<Link to="/">
					<h1 id="siteName">Theophilus</h1>
				</Link>

				{this.state.pageName.map(pageName => (
					<NavLinks
						key={pageName}
						pageName={pageName}
						changeState={this.changeState}
					/>
				))}

				<div id="mobileNav">
					<div id="blocker" onClick={this.openCloseMobileMenu}></div>

					<ul id="panel">
						{this.state.pageName.map(pageName => (
							<NavLinks
								key={pageName}
								pageName={pageName}
								changeState={this.changeState}
								mobile
							/>
						))}
						{/* <NavLinks changeState={this.changeState} mobile /> */}
					</ul>
				</div>

				<div
					onClick={this.openCloseMobileMenu}
					className="navBtn"
					id="mobileMenuButton"
				></div>
			</nav>
		);
	}
}

export default Nav;
