import React, { Component } from "react";
import { Link } from "react-router-dom";

class ProjectGroup extends Component {
	render() {
		return (
				<div className="unit double">
					<hr />
					<br />
					<div className="margin" id={this.props.section}></div>
					<h3 className="cate">
						<strong>{this.props.name}</strong>
					</h3>
					<hr />
					<ul className="project">
						{this.props.data.map(proj => (
							<PageUnit
								key={proj[3]}
								title={proj[0]}
								description={proj[1]}
								imageUrl={proj[2]}
								link={proj[3]}
								external={proj[4]}
							/>
						))}
					</ul>
				</div>
		);
	}
}

class PageUnit extends Component {
	render() {
		// customTag = (this.props.external) ? "a" : "Link"
		// customAtt = (this.props.external) ? "href" : "to"
		if (this.props.external) {
			return (
				<a href={this.props.link}>
					<li>
						<div className="img">
							<img
								src={`/Images/project/${this.props.imageUrl}`}
								alt={this.props.imageUrl}
							/>
						</div>
						<div className="details">
							<h4>{this.props.title}</h4>
							<br />
							<p>{this.props.description}</p>
						</div>
					</li>
				</a>
			);
		}

		return (
			<Link to={`projects/${this.props.link}`}>
				<li>
					<div className="img">
						<img
							src={`/Images/project/${this.props.imageUrl}`}
							alt={this.props.imageUrl}
						/>
					</div>
					<div className="details">
						<h4>{this.props.title}</h4>
						<br />
						<p>{this.props.description}</p>
					</div>
				</li>
			</Link>
		);
	}
}

export default ProjectGroup;
