import React, { Component } from "react";
import { Link } from "react-router-dom";
import "./navLinks.scss";

class NavLinks extends Component {
	setActive = pageName => {
		if (this.props.pageName === pageName) return "active";
		return "";
	};

	project() {
		const proj01 = (
			<Link to="/projects">
				<li
					className={this.setActive("Project")}
					onClick={() => this.props.changeState("Project")}
				>
					Projects
				</li>
			</Link>
		);

		const proj02 = (
			<li className={this.setActive("Project")}>
				Projects
				<ul id="lv2" className="lv2">
					<Link to="/projects#web">
						<li onClick={() => this.props.changeState("Project")}>
							Web
						</li>
					</Link>
					<Link to="/projects#logo">
						<li onClick={() => this.props.changeState("Project")}>
							Logos and Banners
						</li>
					</Link>
					<Link to="/projects#model">
						<li onClick={() => this.props.changeState("Project")}>
							3D
						</li>
					</Link>
					<Link to="/projects#animation">
						<li onClick={() => this.props.changeState("Project")}>
							Animation
						</li>
					</Link>
					<Link to="/projects#others">
						<li onClick={() => this.props.changeState("Project")}>
							Electronics
						</li>
					</Link>
				</ul>
			</li>
		);

		return this.props.mobile ? proj01 : proj02;
	}

	render() {
		return (
			<ul id="navLinks">
				<Link to="">
					<li
						className={this.setActive("Home")}
						onClick={() => this.props.changeState("Home")}
					>
						Home
					</li>
				</Link>
				<Link to="/about">
					<li
						className={this.setActive("About")}
						onClick={() => this.props.changeState("About")}
					>
						About Me
					</li>
				</Link>
				{this.project()}
				<Link to="/contact">
					<li
						className={this.setActive("Contact")}
						onClick={() => this.props.changeState("Contact")}
					>
						Contact
					</li>
				</Link>
			</ul>
		);
	}
}

export default NavLinks;
